import React, { useState } from 'react';
import {
  Box,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Grid,
  Rating,
  Typography
} from '@mui/material';

const AddBook = ({ onSubmit, onCancel }) => {
  const [bookData, setBookData] = useState({
    title: '',
    author: '',
    description: '',
    genre: '',
    imageUrl: '',
    rating: 0,
    publishYear: ''
  });

  const genres = ['Roman', 'Bilim Kurgu', 'Fantastik', 'Macera', 'Gerilim', 'Klasik'];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setBookData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleRatingChange = (event, newValue) => {
    setBookData(prev => ({
      ...prev,
      rating: newValue
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(bookData);
  };

  return (
    <Box component="form" onSubmit={handleSubmit} sx={{ mt: 2 }}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <TextField
            required
            fullWidth
            name="title"
            label="Kitap Adı"
            value={bookData.title}
            onChange={handleChange}
          />
        </Grid>

        <Grid item xs={12}>
          <TextField
            required
            fullWidth
            name="author"
            label="Yazar"
            value={bookData.author}
            onChange={handleChange}
          />
        </Grid>

        <Grid item xs={12}>
          <TextField
            required
            fullWidth
            name="description"
            label="Açıklama"
            multiline
            rows={4}
            value={bookData.description}
            onChange={handleChange}
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <FormControl fullWidth required>
            <InputLabel>Tür</InputLabel>
            <Select
              name="genre"
              value={bookData.genre}
              onChange={handleChange}
              label="Tür"
            >
              {genres.map((genre) => (
                <MenuItem key={genre} value={genre}>
                  {genre}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>

        <Grid item xs={12}>
          <TextField
            required
            fullWidth
            name="imageUrl"
            label="Kitap Kapağı URL"
            value={bookData.imageUrl}
            onChange={handleChange}
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <TextField
            required
            fullWidth
            name="publishYear"
            label="Yayın Yılı"
            type="number"
            value={bookData.publishYear}
            onChange={handleChange}
            inputProps={{ min: 1800, max: new Date().getFullYear() }}
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <Box sx={{ mt: 1 }}>
            <Typography component="legend">Puan</Typography>
            <Rating
              name="rating"
              value={bookData.rating}
              onChange={handleRatingChange}
              precision={0.5}
            />
          </Box>
        </Grid>

        <Grid item xs={12}>
          <Box sx={{ display: 'flex', gap: 2, justifyContent: 'flex-end', mt: 2 }}>
            <Button onClick={onCancel} color="inherit">
              İptal
            </Button>
            <Button type="submit" variant="contained" color="primary">
              Kitap Ekle
            </Button>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};

export default AddBook; 