import React, { useState, useEffect } from 'react';
import { 
  Container, 
  Grid, 
  Typography, 
  CircularProgress, 
  Box,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  Alert
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { bookService } from '../services/api';
import BookCard from '../components/Books/BookCard';
import AddBook from '../components/Books/AddBook';

const BookList = () => {
  const [books, setBooks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isAdmin, setIsAdmin] = useState(false);
  const [openAddDialog, setOpenAddDialog] = useState(false);
  const [addBookSuccess, setAddBookSuccess] = useState(false);

  useEffect(() => {
    fetchBooks();
    checkAdminStatus();
  }, []);

  const checkAdminStatus = () => {
    try {
      const user = JSON.parse(localStorage.getItem('user') || '{}');
      setIsAdmin(user.email === 'mustafa_guneyli21@erdogan.edu.tr');
    } catch (error) {
      console.error('Admin kontrolü hatası:', error);
      setIsAdmin(false);
    }
  };

  const fetchBooks = async () => {
    try {
      setLoading(true);
      const data = await bookService.getAllBooks();
      setBooks(data);
    } catch (error) {
      console.error('Kitaplar yüklenirken hata:', error);
      setError('Kitaplar yüklenirken bir hata oluştu');
    } finally {
      setLoading(false);
    }
  };

  const handleAddBook = async (bookData) => {
    try {
      await bookService.addBook(bookData);
      setAddBookSuccess(true);
      setOpenAddDialog(false);
      fetchBooks(); // Kitap listesini yenile
    } catch (error) {
      console.error('Kitap ekleme hatası:', error);
      setError('Kitap eklenirken bir hata oluştu');
    }
  };

  const handleDeleteBook = async (bookId) => {
    try {
      await bookService.deleteBook(bookId);
      setBooks(prevBooks => prevBooks.filter(book => book._id !== bookId));
    } catch (error) {
      console.error('Kitap silme hatası:', error);
      setError('Kitap silinirken bir hata oluştu');
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Typography variant="h4" component="h1">
          Kitaplar
        </Typography>
        {isAdmin && (
          <Button
            variant="contained"
            color="primary"
            startIcon={<AddIcon />}
            onClick={() => setOpenAddDialog(true)}
          >
            Yeni Kitap Ekle
          </Button>
        )}
      </Box>

      {addBookSuccess && (
        <Alert severity="success" sx={{ mb: 2 }}>
          Kitap başarıyla eklendi!
        </Alert>
      )}

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      <Grid container spacing={3}>
        {books.map((book) => (
          <Grid item xs={12} sm={6} md={4} key={book._id}>
            <BookCard 
              book={book} 
              showAddButton={false}
              onDelete={isAdmin ? handleDeleteBook : undefined}
            />
          </Grid>
        ))}
      </Grid>

      <Dialog 
        open={openAddDialog} 
        onClose={() => setOpenAddDialog(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>Yeni Kitap Ekle</DialogTitle>
        <DialogContent>
          <AddBook 
            onSubmit={handleAddBook}
            onCancel={() => setOpenAddDialog(false)}
          />
        </DialogContent>
      </Dialog>
    </Container>
  );
};

export default BookList; 