import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Grid,
  Box,
  CircularProgress,
  Alert,
  Paper
} from '@mui/material';
import BookCard from '../components/Books/BookCard';
import { bookService, weatherService } from '../services/api';
import WbSunnyIcon from '@mui/icons-material/WbSunny';
import CloudIcon from '@mui/icons-material/Cloud';
import UmbrellaIcon from '@mui/icons-material/BeachAccess';

const Recommendations = () => {
  const [books, setBooks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [weather, setWeather] = useState(null);

  useEffect(() => {
    fetchWeatherAndBooks();
  }, []);

  const fetchWeatherAndBooks = async () => {
    try {
      setLoading(true);
      // Rize için hava durumunu al
      const weatherData = await weatherService.getWeather('Rize');
      setWeather(weatherData);

      // Hava durumuna göre kitap önerileri al
      const weatherCondition = getWeatherCondition(weatherData.weather[0].main);
      const recommendations = await bookService.getWeatherBasedRecommendations(weatherCondition);
      setBooks(recommendations);
    } catch (error) {
      console.error('Veri yükleme hatası:', error);
      setError('Öneriler yüklenirken bir hata oluştu');
    } finally {
      setLoading(false);
    }
  };

  const getWeatherCondition = (weatherMain) => {
    const conditions = {
      'Clear': 'güneşli',
      'Clouds': 'bulutlu',
      'Rain': 'yağmurlu',
      'Snow': 'karlı',
      'Thunderstorm': 'fırtınalı',
      'Drizzle': 'yağmurlu',
      'Mist': 'sisli'
    };
    return conditions[weatherMain] || 'normal';
  };

  const getWeatherIcon = (weatherMain) => {
    switch (weatherMain) {
      case 'Clear':
        return <WbSunnyIcon sx={{ color: '#FFD700' }} />;
      case 'Clouds':
        return <CloudIcon sx={{ color: '#808080' }} />;
      case 'Rain':
      case 'Drizzle':
        return <UmbrellaIcon sx={{ color: '#4169E1' }} />;
      default:
        return <WbSunnyIcon />;
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      {weather && (
        <Paper elevation={3} sx={{ p: 3, mb: 4, bgcolor: 'background.default' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            {getWeatherIcon(weather.weather[0].main)}
            <Typography variant="h6">
              Rize: {Math.round(weather.main.temp)}°C, {weather.weather[0].description}
            </Typography>
          </Box>
          <Typography variant="body1" sx={{ mt: 1 }}>
            Bugün hava {getWeatherCondition(weather.weather[0].main)} olduğu için size uygun kitap önerileri:
          </Typography>
        </Paper>
      )}

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      <Grid container spacing={3}>
        {books.map((book) => (
          <Grid item xs={12} sm={6} md={4} key={book._id}>
            <BookCard book={book} />
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default Recommendations; 