import React, { useState } from 'react';
import { 
  Card, 
  CardContent, 
  CardMedia, 
  Typography, 
  Rating, 
  Box,
  IconButton,
  Tooltip,
  Chip,
  Snackbar,
  Alert
} from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import DeleteIcon from '@mui/icons-material/Delete';
import { bookService } from '../../services/api';
import { useNavigate } from 'react-router-dom';

const BookCard = ({ book, showAddButton = true, onDelete }) => {
  const navigate = useNavigate();
  const isLoggedIn = localStorage.getItem('token');
  const [imageError, setImageError] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success'
  });

  const handleAddToList = async () => {
    if (!isLoggedIn) {
      navigate('/login');
      return;
    }

    try {
      const response = await bookService.addToUserList({
        bookId: book._id,
        title: book.title,
        author: book.author,
        description: book.description,
        genre: book.genre,
        imageUrl: book.imageUrl,
        rating: book.rating,
        publishYear: book.publishYear
      });

      setSnackbar({
        open: true,
        message: 'Kitap listenize eklendi!',
        severity: 'success'
      });
    } catch (error) {
      if (error.response?.data?.message === 'Bu kitap zaten listenizde var') {
        setSnackbar({
          open: true,
          message: 'Bu kitap zaten listenizde var!',
          severity: 'warning'
        });
      } else {
        setSnackbar({
          open: true,
          message: 'Kitap eklenirken bir hata oluştu',
          severity: 'error'
        });
      }
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  return (
    <>
      <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column', position: 'relative' }}>
        <CardMedia
          component="img"
          height="200"
          image={book.imageUrl || '/images/book-placeholder.jpg'}
          alt={book.title}
          onError={() => setImageError(true)}
        />
        <CardContent>
          <Typography gutterBottom variant="h6" component="div">
            {book.title}
          </Typography>
          <Typography variant="body2" color="text.secondary" gutterBottom>
            {book.author}
          </Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
            <Rating value={book.rating} precision={0.5} readOnly size="small" />
          </Box>
          <Chip 
            label={book.genre} 
            size="small" 
            color="primary" 
            variant="outlined" 
            sx={{ mb: 1 }}
          />
          <Typography variant="body2" color="text.secondary">
            {book.description}
          </Typography>
        </CardContent>

        {showAddButton && isLoggedIn && (
          <Tooltip title="Listeme Ekle">
            <IconButton
              onClick={handleAddToList}
              sx={{
                position: 'absolute',
                top: 8,
                right: 8,
                bgcolor: 'background.paper',
                '&:hover': {
                  bgcolor: 'primary.main',
                  color: 'white'
                }
              }}
            >
              <AddCircleIcon />
            </IconButton>
          </Tooltip>
        )}

        {onDelete && (
          <Tooltip title="Listeden Kaldır">
            <IconButton
              onClick={() => onDelete(book._id)}
              sx={{
                position: 'absolute',
                top: 8,
                right: 8,
                bgcolor: 'background.paper',
                '&:hover': {
                  bgcolor: 'error.main',
                  color: 'white'
                }
              }}
            >
              <DeleteIcon />
            </IconButton>
          </Tooltip>
        )}
      </Card>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={handleCloseSnackbar} 
          severity={snackbar.severity}
          variant="filled"
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default BookCard; 