import React, { useState, useEffect } from 'react';
import { 
  Container, 
  Typography, 
  Grid, 
  Box, 
  CircularProgress,
  Alert
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import BookCard from '../components/Books/BookCard';
import { bookService } from '../services/api';

const UserBookList = () => {
  const [userBooks, setUserBooks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const isLoggedIn = localStorage.getItem('token');
    if (!isLoggedIn) {
      navigate('/login');
      return;
    }
    fetchUserBooks();
  }, [navigate]);

  const fetchUserBooks = async () => {
    try {
      setLoading(true);
      const data = await bookService.getUserBooks();
      setUserBooks(data);
      setError(null);
    } catch (error) {
      console.error('Kitaplar yüklenirken hata:', error);
      setError('Kitaplarınız yüklenirken bir hata oluştu');
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteBook = async (bookId) => {
    try {
      await bookService.removeFromUserList(bookId);
      setUserBooks(prevBooks => prevBooks.filter(book => book._id !== bookId));
    } catch (error) {
      console.error('Kitap silinirken hata:', error);
      setError('Kitap silinirken bir hata oluştu');
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        Kitap Listem
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      {userBooks.length === 0 ? (
        <Alert severity="info">
          Henüz listenizde kitap yok. Kitaplar sayfasından kitap ekleyebilirsiniz.
        </Alert>
      ) : (
        <Grid container spacing={3}>
          {userBooks.map((book) => (
            <Grid item xs={12} sm={6} md={4} key={book._id}>
              <BookCard 
                book={book} 
                showAddButton={false}
                onDelete={handleDeleteBook}
              />
            </Grid>
          ))}
        </Grid>
      )}
    </Container>
  );
};

export default UserBookList; 