import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'https://okurkosesi.onrender.com/api';

const axiosInstance = axios.create({
  baseURL: API_URL,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json'
  }
});

// Request interceptor - debug için
axiosInstance.interceptors.request.use(
  (config) => {
    console.log('İstek gönderiliyor:', config.url, config.data);
    return config;
  },
  (error) => {
    console.error('İstek hatası:', error);
    return Promise.reject(error);
  }
);

// Response interceptor - debug için
axiosInstance.interceptors.response.use(
  (response) => {
    console.log('Yanıt alındı:', response.data);
    return response;
  },
  (error) => {
    console.error('Yanıt hatası:', error.response?.data || error);
    return Promise.reject(error);
  }
);

// Debug için API URL'ini konsola yazdır
console.log('API URL:', API_URL);

// Auth servisi
export const authService = {
  register: async (userData) => {
    try {
      console.log('Register isteği gönderiliyor:', userData);
      const response = await axiosInstance.post('/users/register', userData);
      console.log('Register yanıtı:', response.data);
      return response.data;
    } catch (error) {
      console.error('Register hatası:', error.response?.data || error);
      throw error;
    }
  },

  login: async (credentials) => {
    try {
      console.log('Login isteği gönderiliyor:', credentials);
      const response = await axiosInstance.post('/users/login', credentials);
      
      // Token ve kullanıcı bilgilerini localStorage'a kaydet
      if (response.data.token) {
        localStorage.setItem('token', response.data.token);
        localStorage.setItem('user', JSON.stringify(response.data.user));
      }
      
      console.log('Login yanıtı:', response.data);
      return response.data;
    } catch (error) {
      console.error('Login hatası:', error.response?.data || error);
      throw error;
    }
  }
};

export const bookService = {
  getAllBooks: async () => {
    try {
      console.log('Kitaplar getiriliyor...');
      const response = await axiosInstance.get('/books');
      console.log(`${response.data.length} kitap getirildi`);
      return response.data;
    } catch (error) {
      console.error('Kitapları getirme hatası:', error);
      throw error;
    }
  },

  searchBooks: async (query) => {
    try {
      const response = await axiosInstance.get(`${API_URL}/books/search`, {
        params: { query }
      });
      return response.data;
    } catch (error) {
      console.error('Arama hatası:', error);
      throw error;
    }
  },

  getRecommendations: async (genre) => {
    try {
      const params = { city: 'Rize' };
      if (genre && genre !== 'Tümü') {
        params.genre = genre;
      }
      
      const response = await axiosInstance.get('/recommendations', { params });
      return response.data;
    } catch (error) {
      console.error('Öneriler getirme hatası:', error);
      throw error;
    }
  },

  addBook: async (bookData) => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        throw new Error('Yetkilendirme gerekli');
      }

      console.log('Kitap ekleniyor:', bookData);
      const response = await axiosInstance.post('/books', bookData, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      console.log('Kitap başarıyla eklendi:', response.data);
      return response.data;
    } catch (error) {
      console.error('Kitap ekleme hatası:', error);
      throw error;
    }
  },

  getUserBooks: async () => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        console.log('Token bulunamadı');
        return [];
      }

      const response = await axiosInstance.get('/books/user-books', {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      return response.data;
    } catch (error) {
      console.error('Kullanıcı kitapları getirme hatası:', error);
      // 401 hatası gelirse sessizce boş array dön
      if (error.response?.status === 401) {
        return [];
      }
      throw error;
    }
  },

  addToUserList: async (bookData) => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        throw new Error('Giriş yapmanız gerekiyor');
      }

      const response = await axiosInstance.post('/books/add-to-list', bookData, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      console.log('Kitap listeye eklendi:', response.data);
      return response.data;
    } catch (error) {
      console.error('Kitap ekleme hatası:', error);
      throw error;
    }
  },

  removeFromUserList: async (bookId) => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        throw new Error('Giriş yapmanız gerekiyor');
      }

      const response = await axiosInstance.delete(`/books/user-books/${bookId}`, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      return response.data;
    } catch (error) {
      console.error('Kitap silme hatası:', error);
      throw error;
    }
  },

  deleteBook: async (bookId) => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        throw new Error('Yetkilendirme gerekli');
      }

      const response = await axiosInstance.delete(`/books/${bookId}`, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      return response.data;
    } catch (error) {
      console.error('Kitap silme hatası:', error);
      throw error;
    }
  },

  getWeatherBasedRecommendations: async (weather) => {
    try {
      const response = await axiosInstance.get('/books/recommendations/weather', {
        params: { weather }
      });
      return response.data;
    } catch (error) {
      console.error('Hava durumuna göre öneriler getirme hatası:', error);
      throw error;
    }
  }
};

// Weather servisi
export const weatherService = {
  getWeather: async (city) => {
    try {
      const response = await axios.get(
        `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=f8654d44f083bb8641071c003cbb0cb9&units=metric&lang=tr`
      );
      return response.data;
    } catch (error) {
      console.error('Hava durumu getirme hatası:', error);
      throw error;
    }
  }
}; 